* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  color: #333;
  background-color: #f4f4f9;
  padding-bottom: 50px;
  line-height: 1.6;
}

header {
  color: #fff;
  text-align: center;
  background-color: #004080;
  padding: 20px 0;
}

header h1 {
  margin-bottom: 10px;
  font-size: 1.8rem;
}

nav ul {
  justify-content: center;
  gap: 20px;
  padding: 10px 0;
  list-style: none;
  display: flex;
}

nav ul li a {
  color: #fff;
  border-radius: 5px;
  padding: 5px 10px;
  font-weight: bold;
  text-decoration: none;
}

nav ul li a.active, nav ul li a:hover {
  background-color: #06c;
}

.events-section {
  max-width: 900px;
  margin: 30px auto;
  padding: 0 20px;
}

article.event {
  background-color: #fff;
  border-radius: 8px;
  margin-bottom: 25px;
  padding: 20px;
  box-shadow: 0 2px 6px #0000001a;
}

article.event.highlight {
  border-left: 5px solid #004080;
}

article.event h2 {
  color: #004080;
  margin-bottom: 10px;
}

article.event p {
  margin-bottom: 10px;
}

article.event form {
  flex-direction: column;
  gap: 10px;
  margin-top: 15px;
  display: flex;
}

article.event input, article.event select {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 8px;
}

article.event button {
  color: #fff;
  cursor: pointer;
  background-color: #004080;
  border: none;
  border-radius: 5px;
  width: 120px;
  padding: 10px;
}

article.event button:hover {
  background-color: #06c;
}

footer {
  text-align: center;
  color: #fff;
  background-color: #004080;
  width: 100%;
  margin-top: 30px;
  padding: 15px 0;
  position: fixed;
  bottom: 0;
}

@media (width <= 600px) {
  nav ul {
    flex-direction: column;
  }
}
