/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

/* Body */
body {
  background-color: #f4f4f9;
  color: #333;
  line-height: 1.6;
  padding-bottom: 50px;
}

/* Header */
header {
  background-color: #004080;
  color: white;
  padding: 20px 0;
  text-align: center;
}

header h1 {
  margin-bottom: 10px;
  font-size: 1.8rem;
}

/* Navigation */
nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 10px 0;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  padding: 5px 10px;
  border-radius: 5px;
}

nav ul li a.active,
nav ul li a:hover {
  background-color: #0066cc;
}

/* Main Section */
.events-section {
  max-width: 900px;
  margin: 30px auto;
  padding: 0 20px;
}

/* Event Article */
article.event {
  background-color: white;
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 25px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

article.event.highlight {
  border-left: 5px solid #004080;
}

article.event h2 {
  margin-bottom: 10px;
  color: #004080;
}

article.event p {
  margin-bottom: 10px;
}

/* Form inside event */
article.event form {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

article.event input,
article.event select {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

article.event button {
  width: 120px;
  padding: 10px;
  background-color: #004080;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

article.event button:hover {
  background-color: #0066cc;
}

/* Footer */
footer {
  text-align: center;
  margin-top: 30px;
  padding: 15px 0;
  background-color: #004080;
  color: white;
  position: fixed;
  width: 100%;
  bottom: 0;
}

/* Responsive */
@media (max-width: 600px) {
  nav ul {
    flex-direction: column;
  }
}
